export default {
  name: 'Video',
  props: {
    videoUrl: {
      // 视频文件url
      type: String,
      default: null,
      required: true
    },
    videoCover: {
      // 视频封面url
      type: String,
      default: null
    },
    width: {
      // 视频播放器宽度
      type: Number,
      default: 800
    },
    height: {
      // 视频播放器高度
      type: Number,
      default: 450
    },
    /*
      由于目前在最新版的Chrome浏览器（以及所有以Chromium为内核的浏览器）中，
      已不再允许自动播放音频和视频。就算你为video或audio标签设置了autoplay
      属性也一样不能自动播放！
      本组件解决方法：设置视频 autoplay 时，自动将视频设置为静音 muted: true 即可实现自
      动播放，然后用户可以使用控制栏开启声音，类似某宝商品自动播放的宣传视频逻辑
    */
    autoplay: {
      // 视频就绪后是否马上播放
      type: Boolean,
      default: false
    },
    controls: {
      // 是否向用户显示控件，比如进度条，全屏
      type: Boolean,
      default: true
    },
    loop: {
      // 视频播放完成后，是否循环播放
      type: Boolean,
      default: false
    },
    muted: {
      // 是否静音
      type: Boolean,
      default: false
    },
    preload: {
      // 是否在页面加载后载入视频，如果设置了autoplay属性，则preload将被忽略；
      type: String,
      default: 'auto' // auto:一旦页面加载，则开始加载视频; metadata:当页面加载后仅加载视频的元数据 none:页面加载后不应加载视频
    },

    playWidth: {
      // 中间播放暂停按钮的边长
      type: Number,
      default: 96
    },
    zoom: {
      // video的poster默认图片和视频内容缩放规则
      type: String,
      default: 'none' // none:(默认)保存原有内容，不进行缩放; fill:不保持原有比例，内容拉伸填充整个内容容器; contain:保存原有比例，内容以包含方式缩放; cover:保存原有比例，内容以覆盖方式缩放
    }
  },

  data() {
    return {
      originPlay: true,
      vplay: true
    };
  },
  mounted() {
    this.$refs.veo.addEventListener('pause', this.onPause);
    this.$refs.veo.addEventListener('playing', this.onPlaying);
    if (this.autoplay) {
      this.vplay = false;
      this.originPlay = false;
    }
  },
  methods: {
    onPause() {
      this.vplay = true;
      this.$once('hook:beforeDestroy', function () {
        removeEventListener('pause', this.onPause);
      });
    },
    onPlaying() {
      this.vplay = false;
      // 自动清理自己，避免内存泄漏
      this.$once('hook:beforeDestroy', function () {
        removeEventListener('playing', this.onPlaying);
      });
    },
    clickPlay() {
      this.$refs.veo.play();
      this.vplay = false;
      if (this.originPlay) {
        this.originPlay = false;
      }
    }
  }
};