export default {
  data() {
    return {
      audio: null,
      circleLeft: null,
      barWidth: null,
      duration: null,
      currentTime: null,
      isTimerPlaying: false,
      tracks: [{
        name: "秘密",
        artist: "范丞丞",
        cover: "https://img1.baidu.com/it/u=1593818572,2271045971&fm=253&fmt=auto&app=138&f=JPEG?w=1062&h=462",
