        url: "https://www.bilibili.com/video/BV1Af4y137Kh/?spm_id_from=333.337.search-card.all.click",
        favorited: false
      }, {
        name: "凭什么说",
        artist: "范丞丞",
        cover: "https://img2.baidu.com/it/u=2984276817,1318083749&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=750",
        source: "http://music.163.com/song/media/outer/url?id=1958106154.mp3",
        url: "https://www.bilibili.com/video/BV1dL411H7rC/?spm_id_from=333.337.search-card.all.click",
        favorited: true
      }, {
        name: "Missed Texts",
        artist: "范丞丞",
        cover: "https://img2.baidu.com/it/u=806003040,423412536&fm=253&fmt=auto?w=762&h=500",
        source: "http://music.163.com/song/media/outer/url?id=1454714032.mp3",
        url: "https://www.bilibili.com/video/BV1qy4y1k7Hw/?spm_id_from=333.337.search-card.all.click",
        favorited: false
      }, {
        name: "过度呼吸",
        artist: "范丞丞",
        cover: "https://img1.baidu.com/it/u=54826339,3717073452&fm=253&fmt=auto&app=138&f=JPEG?w=750&h=500",
        source: "http://music.163.com/song/media/outer/url?id=1454714055.mp3",
        url: "https://www.bilibili.com/video/BV1BK411p7f3/?spm_id_from=333.337.search-card.all.click",
        favorited: false
      }
      // ,
      // {
      //   name: "哑剧",
      //   artist: "范丞丞",
      //   cover: "https://img0.baidu.com/it/u=2813131997,494733500&fm=253&fmt=auto&app=138&f=PNG?w=500&h=283",
      //   source: "https://m10.music.126.net/20221207000014/d7f0b7bb73d5b0860fe5b81b3c09e6a7/yyaac/obj/wonDkMOGw6XDiTHCmMOi/14054058573/cffd/3285/c174/8049bb7e6d722bd3aed7590d3c6ab178.m4a",
      //   url: "https://www.bilibili.com/video/BV1ct411v7oU/?spm_id_from=333.337.search-card.all.click",
      //   favorited: true
      // },
      // {
      //   name: "Dip Out",
      //   artist: "范丞丞",
      //   cover: "https://img2.baidu.com/it/u=3034754768,2406600253&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=281",
      //   source: "http://music.163.com/song/media/outer/url?id=1370858194.mp3",
      //   url: "https://www.bilibili.com/video/BV1WE411J7wr/?spm_id_from=333.337.search-card.all.click",
      //   favorited: false
      // }
      ],

      currentTrack: null,
      currentTrackIndex: 0,
      transitionName: null
    };
  },
  methods: {
    play() {
      if (this.audio.paused) {
        this.audio.play();
        this.isTimerPlaying = true;
      } else {
        this.audio.pause();
        this.isTimerPlaying = false;
      }
    },
    generateTime() {
      let width = 100 / this.audio.duration * this.audio.currentTime;
      this.barWidth = width + "%";
      this.circleLeft = width + "%";
      let durmin = Math.floor(this.audio.duration / 60);
      let dursec = Math.floor(this.audio.duration - durmin * 60);
      let curmin = Math.floor(this.audio.currentTime / 60);
      let cursec = Math.floor(this.audio.currentTime - curmin * 60);
      if (durmin < 10) {
        durmin = "0" + durmin;
      }
      if (dursec < 10) {
        dursec = "0" + dursec;
      }
      if (curmin < 10) {
        curmin = "0" + curmin;
      }
      if (cursec < 10) {
        cursec = "0" + cursec;
      }
      this.duration = durmin + ":" + dursec;
      this.currentTime = curmin + ":" + cursec;
    },
    updateBar(x) {
      let progress = this.$refs.progress;
      let maxduration = this.audio.duration;
      let position = x - progress.offsetLeft;
      let percentage = 100 * position / progress.offsetWidth;
      if (percentage > 100) {
        percentage = 100;
      }
      if (percentage < 0) {
        percentage = 0;
      }
      this.barWidth = percentage + "%";
      this.circleLeft = percentage + "%";
      this.audio.currentTime = maxduration * percentage / 100;
      this.audio.play();
    },
    clickProgress(e) {
      this.isTimerPlaying = true;
      this.audio.pause();
      this.updateBar(e.pageX);
    },
    prevTrack() {
      this.transitionName = "scale-in";
      this.isShowCover = false;
      if (this.currentTrackIndex > 0) {
        this.currentTrackIndex--;
      } else {
        this.currentTrackIndex = this.tracks.length - 1;
      }
      this.currentTrack = this.tracks[this.currentTrackIndex];
      this.resetPlayer();
    },
    nextTrack() {
      this.transitionName = "scale-out";
      this.isShowCover = false;
      if (this.currentTrackIndex < this.tracks.length - 1) {
        this.currentTrackIndex++;
      } else {
        this.currentTrackIndex = 0;
      }
      this.currentTrack = this.tracks[this.currentTrackIndex];
      this.resetPlayer();
    },
    resetPlayer() {
      this.barWidth = 0;
      this.circleLeft = 0;
      this.audio.currentTime = 0;
      this.audio.src = this.currentTrack.source;
      setTimeout(() => {
        if (this.isTimerPlaying) {
          this.audio.play();
        } else {
          this.audio.pause();
        }
      }, 300);
    },
    favorite() {
      this.tracks[this.currentTrackIndex].favorited = !this.tracks[this.currentTrackIndex].favorited;
    }
  },
  created() {
    let vm = this;
    this.currentTrack = this.tracks[0];
    this.audio = new Audio();
    this.audio.src = this.currentTrack.source;
    this.audio.ontimeupdate = function () {
      vm.generateTime();
    };
    this.audio.onloadedmetadata = function () {
      vm.generateTime();
    };
    this.audio.onended = function () {
      vm.nextTrack();
      this.isTimerPlaying = true;
    };

    // this is optional (for preload covers)
    for (let index = 0; index < this.tracks.length; index++) {
      const element = this.tracks[index];
      let link = document.createElement('link');
      link.rel = "prefetch";
      link.href = element.cover;
      link.as = "image";
      document.head.appendChild(link);
    }
  }
};